import axios from "axios"

function Signup (obj){
   this.obj = obj
   axios.post("http://localhost:3080/api/insertUser",this.obj).then(()=>{
        alert("Data saved")
      }).catch(function (err) {
        console.log(err.response.data.message);
      });
}


export default function CreateUser (){
    this.postRequest=(type,obj)=>{
        switch (type) {
            case "Signup":
                return new Signup(obj)
            default:
                break;
        }
    }
}

