import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Paper,Grid,TextField,Select,MenuItem,Button} from '@material-ui/core';
import ReportButton from './ReportButton';
import CreateUser from "../DesignPattern/FactoryDesign";
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function SignUp() {
  const classes = useStyles();
  const [FirstName, setFirstName] = useState("");
  const [LastName, setLastName] = useState("");
  const [Email, setEmail] = useState("");
  const [Gender, setGender] = useState("");
  const [Birth, setBirth] = useState("");
const checking  = ()=>{
    if(FirstName&&LastName&&Email){
      const factory = new CreateUser()
      factory.postRequest("Signup",{
        firstName:FirstName,
        lastName:LastName,
        email:Email,
        gender:Gender,
        birth:Birth
      })
    }else{
        alert("please fill all the fields")
    }
}
  return (
    <div className={classes.root}>
      <Paper style={{padding:20,marginTop:20}}>
          <h1>Sign Up</h1>
        <Grid container spacing={3} style={{textAlign:"center"}}>
            <Grid item xs={12} >
                <TextField id="First-Name"  value={FirstName}label="First Name" style={{width:"100%"}} onChange={(val)=>{setFirstName(val.target.value)}}/>
            </Grid>
            <Grid item xs={6} >
                <TextField id="Last-Name" value={LastName} label="Last Name" style={{width:"100%"}} onChange={(val)=>{setLastName(val.target.value)}}/>
            </Grid>
            <Grid item xs={6} >
                <TextField id="Email" value={Email} label="Email" style={{width:"100%"}} onChange={(val)=>{setEmail(val.target.value)}}/>
            </Grid>
            <Grid item xs={12} >
                <TextField id="dob" type="date" value={Birth} style={{width:"100%"}} onChange={(val)=>{setBirth(val.target.value)}}/>
            </Grid>
            <Grid item xs={12} >
                <Select
                labelId="Gender"
                id="demo-simple-select"
                style={{width:"100%",marginTop:15}}
                value={Gender}
                onChange={(val)=>{setGender(val.target.value)}}
                >
                    <MenuItem value="Male">Male</MenuItem>
                    <MenuItem value="Female">Female</MenuItem>
                </Select>
            </Grid>
            <Button onClick={checking} variant="outlined">Submit</Button>
        </Grid>
      </Paper>
      <ReportButton/>
    </div>
  );
}
