import React, { useEffect,useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import axios from "axios"
import Chart from "../Component/Chart"
import { TableComponent } from '../Component/Table';
import {Grid} from "@material-ui/core"
import getAge from '../API/CalcAge';


function createData(id, firstName, lastName, email, gender, birth) {
  return { id, firstName, lastName, email, gender,birth };
}


export function Report() {
    const [data, setdata] = useState([]);
    const male = []
    const female = []
    const young = []
    const old = []
    useEffect(()=>{
        axios.get("http://localhost:3080/api/getUser").then((result)=>{
            setdata(result.data)
        }).catch((err)=>{
            console.log(err.message)
        })
    },[])
  const rows = [];
  data && data.map((val)=>{
    rows.push(createData(val.id,val.firstName,val.lastName,val.email,val.gender,val.birth))
    if(val.gender === "Female")
      female.push(createData(val.id,val.firstName,val.lastName,val.email,val.gender,val.birth))
    else
      male.push(createData(val.id,val.firstName,val.lastName,val.email,val.gender,val.birth))

    getAge(val.birth) < 31 ? young.push(val.firstName):old.push(val.firstName)
  })

  const genderChartData = [
    { name: 'Male', value: male.length },
    { name: 'Female', value: female.length },
  ];

  const ageChartData = [
    { name: '20-30', value: young.length },
    { name: '31-40', value: old.length },
  ];

  return (
    <>
    <h4 style={{textAlign:"center"}}>Users</h4>
    <TableComponent rows={rows}/> 
    <Grid container spacing={3} style={{textAlign:"center"}}>
      <Grid item xs={6}>
        <Chart data={genderChartData}/>
      </Grid>
      <Grid item xs={6}>
        <Chart data={ageChartData}/>
      </Grid>
    </Grid>
    <Grid container spacing={3}>

      <Grid item xs={12}>
        <h4 style={{textAlign:"center"}}>Female</h4>
        <TableComponent rows={female}/>
      </Grid>
      
      <Grid item xs={12}>
        <h4 style={{textAlign:"center"}}>Male</h4>
        <TableComponent rows={male}/>
      </Grid>

    </Grid>    
    </>
  );
}
